import React, { useRef } from 'react';
import './App.css';

import UseFleekPay from "fleek-checkout";

function App() {

  const childRef = useRef(null);

  const handlePaymentModalSKU = () => {
    const clientId = document.getElementById("clientId");
    const clientSecret = document.getElementById("clientSecret");
    const orderId = document.getElementById("orderId");
    const phoneNo = document.getElementById("phoneNo");
    const emailAddress = document.getElementById("emailAddress");

    const optionsWithoutSKU = {
      clientID: clientId.value, // Enter Client Key ID
      clientSecret: clientSecret.value, // Enter Client Secret Key
      orderId: orderId.value,
      phoneNo: phoneNo.value,
      emailAddress: emailAddress.value
      // skuDetails: {
      //   add_on_amount: 0, // Enter add-on ammount
      //   duration: 1, // Enter duration
      //   duration_type: "M", // Enter duration type
      //   name: "Fleek", //  Enter plan name
      //   amount: 0, // Enter amount
      //   product_image: "https://thetechportal.com/wp-content/uploads/2021/09/logo_vertical_blue.jpg" // Enter image url
      // }
    };

    childRef.current.openPaymentModal(optionsWithoutSKU);
  };

  const checkPaymentStatus = function (paymentStatus) {
    console.log('fleek payment status', paymentStatus);
  }

  return (
    <div className="App">
      <div className='container-btn'>
        <div className='sku-container'>
          <div>
            <input id='clientId' defaultValue={'cnViYW5iaGFyYXRoMTRAZ21haWwuY29t'} placeholder='Enter Client ID' className='sku-input' autoComplete='off'></input>
          </div>
          <div>
            <input id='clientSecret' defaultValue={'yxzQtYonAP3lDsILOab2CBerHhzkFtv1'} placeholder='Enter Client Secret' className='sku-input' autoComplete='off'></input>
          </div>
          <div>
            <input id='orderId' defaultValue={'77efb45b-6c96-4e92-9f07-60c9686236aa'} placeholder='Enter Order ID' className='sku-input' autoComplete='off'></input>
          </div>
          <div>
            <input id='phoneNo' defaultValue={'8610233729'} placeholder='Enter Phone Number' className='sku-input' autoComplete='off'></input>
          </div>
          <div>
            <input id='emailAddress' defaultValue={'test@gmail.com'} placeholder='Enter Email Address' className='sku-input' autoComplete='off'></input>
          </div>
        </div>

        {/* <button className='pay-btn' onClick={handlePaymentModal}>Open With SKU Details</button> */}
        <button className='pay-btn' onClick={handlePaymentModalSKU}>Checkout</button>
      </div>
      <UseFleekPay ref={childRef} checkout={checkPaymentStatus} />
    </div>
  );
}

export default App;
